.TopMonumentsContainer{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-top: 80px;
    gap: 60px;
}
.TopMonumentsHeading{
    font-size: 4rem;
    color: #000000;
    font-weight: 400;
    letter-spacing: 2px;
}
.GlimpseContainer{
    width: fit-content;
    height: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30px;
    transition: all .5s;
}
.Monuments{
    width: 420px;
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    transition: all .5s;
}
.MonumentsImageContainer{
    width: 100%;
    height: 420px;
    position: relative;
    overflow: hidden;
}
.Monuments img{
    width: 100%;
    z-index: 2;
    transition: all .5s;
}
.Monuments p{
    font-size: 1.7rem;
    font-weight: 400;
    color: black;
}
.Monuments button{
    padding: 8px 20px;
    border: none;
    background-color: black;
    color: white;
    cursor: pointer;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 7px;
    border: 1px solid black;
    transition: all .5s;
    
}
.Monuments button svg{
    fill: white;
    height: 10  0%;
    transition: all .5s;
}
.Monuments:hover .MonumentsImageContainer img{
    transform: scale(1.1);
    transform-origin: center;
}
.Monuments button:hover{
    background-color: rgb(253, 253, 253);
    color: black;
}
.Monuments button:hover svg{
    fill: black ;
}



